.ModalVote__overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    display: flex;
    justify-content: center;
    align-items: center;
}

.ModalVote {
    height: 25rem;
    width: 30rem;
    background-color: #fff;
    border-radius: 5px;
    user-select: none;
}

.ModalVote__header {
    height: 2.5rem;
    padding: 2px;
    display: flex;
    justify-content: flex-end;
}

.btn__close {
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    display: flex;

}

.btn__close__container {
    height: 2.5rem;
    width: 2.5rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn__close__container:hover {
    background-color: rgba(0, 0, 0, 0.1);
    transition: .3s;
}

.btn__close  {
    background-image: url('../../images//close.svg');
    background-size: cover;
    cursor: pointer;
}

.ModalVote__body h1{
    margin: 0;
    color: #e93c4e;
    font-size: 2.5rem;
}

.ModalVote__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.body__img {
    height: 15rem;
    width: 15rem;
    margin-top: 20px;
}