:root {
  --primary-color: #e93c4e;
  --secundary-color: #277cad;
  --third-color: #56bb52;
  --light-color: #EDDBD9;
}

.main__Candidates {
  height: 80vh;
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 600ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 600ms ease-in;
}
