.Modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Montserrat-r;
}

.Modal {
  height: 33rem;
  width: 65rem;
  background-color: #fff;
  border-radius: 5px;
}

.Modal.active {
  transition: 0.5s;
  background-color: rgb(228, 42, 42);
}

.Modal__header {
  height: 2.5rem;
  padding: 2px;
  display: flex;
  justify-content: flex-end;
}

.btn__close {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  display: flex;
}

.btn__close__container {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn__close__container:hover {
  background-color: rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}

.btn__close {
  background-image: url("../../images//close.svg");
  background-size: cover;
  cursor: pointer;
}

.main {
  height: 25rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.candidate {
  height: 20rem;
  width: 14rem;
  margin: 0 1rem 0 1rem;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.3);
}

.candidate__header {
  height: 16%;
  width: 100%;
  border-radius: 5px 5px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--third-color);
  color: #f5fffe;
  user-select: none;
}

.candidate__body {
  height: 65%;
  width: 100%;
}

.candidate__img {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

.candidate__footer {
  height: 19%;
  width: 100%;
  border-radius: 0 0 5px 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--third-color);
  color: #f5fffe;
  
}

.main__results h1 {
  color: var(--third-color);
  text-align: center;
  font-family: Montserrat-m;
}
