.Candidate {
    height: 20rem;
    width: 14rem;
    margin: 0 1rem 0 1rem;
    border-radius: 5px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    will-change: transform, transition;
    transition: .4s;
    font-family: Montserrat-r;
}

.Candidate:hover {
    transform: scale(1.2);
}

.Candidate__name h2 {
    margin: 0;
}

.Candidate__name {
    height: 16%;
    width: 100%;
    border-radius: 5px 5px 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secundary-color);
    color: #fff;
    user-select: none;
}

.Candidate__body {
    height: 65%;
    width: 100%;
}

.Candidate__img {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
}


.Candidate__btn {
    height: 19%;
    width: 100%;
    border-radius: 0 0 5px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secundary-color);
}

.btn__vote {
    height: 2.5rem;
    width: 8rem;
    border-radius: 20px;
    background-color: #e93c4e;
    border-bottom: 5px solid #a51f2d;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    cursor: pointer;
}

.btn__vote:active {
    margin-top: 2px;
    border-bottom: none;
    border-bottom: 2.5px solid #551c1e;
}