.Navbar {
    height: 5rem;
    width: 100%;
    background: var(--primary-color);
    display: flex;
    justify-content: space-between;
    user-select: none;
    color: #fff;
    font-family: Montserrat-b;
}

.Navbar__logo {
    height: 5rem;
    width: 5rem;
    background-image: url('../../images/logo.png');
    background-size: cover;
    margin-left: 2rem;
}

.Navbar__btnResults {
    height: 5rem;
    width: 6rem;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-right: 2rem;
    transition: .3s;
    cursor: pointer;
    font-family: Montserrat-l;
}

.Navbar__btnResults:hover {
    background-color: #a51f2d;
}

.Navbar__ico {
    height: 2rem;
    width: 2rem;
    background-image: url('../../images/doc.png');
    background-size: cover;
}

.Navbar__btnResults h4 {
    margin: 0;
}